.Quiz-content__select{
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  padding: 20px;
  width: 100%;
  -webkit-flex: 0.8 1;
          flex: 0.8 1;
  -webkit-justify-content: space-evenly;
          justify-content: space-evenly;
}

